@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
* {
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

h1 {
  padding-bottom: 40px; }

body {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  background-image: -webkit-linear-gradient(330deg, #3498db, #8e44ad);
  background-image: linear-gradient(120deg, #3498db, #8e44ad); }

.button {
  font-size: 1.4em;
  background-color: #f0f0f0;
  border: none;
  border-right: solid 1px #888;
  border-bottom: solid 1px #888;
  outline: none; }

.button:active {
  background-color: #ccc; }

.button.double {
  grid-column: span 2; }

.button.triple {
  grid-column: span 3; }

.button.operation {
  background-color: #fa8231;
  color: white; }

.button.operation:active {
  background-color: #fa8231cc; }

.display {
  grid-column: span 4;
  background-color: #0004;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 20px;
  font-size: 2.2em;
  overflow: hidden; }

.calculator {
  height: 320px;
  width: 263px;
  border-radius: 5px;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-template-rows: 1fr repeat(5, 48px); }

